import "../../Tables.module.css";
import React from "react";
import { useParams } from "react-router-dom";
import { Page, PageContentBase, PageHeader } from "components/pages/Page";
import {
  AppToken,
  CustomColumns,
  Identity,
  Job,
  JobQuery,
  Schedule,
  Script,
} from "types";
import { Card, Col, Space, Tabs, Tag } from "antd";
import ResourceInfo from "components/ui/ResourceInfo";
import { Layout } from "antd";
import Table from "components/ui/Table";
import CopyButton from "components/ui/CopyButton";
import DeleteButton from "components/ui/DeleteButton";
import { capitalize, formatDate, setStatusTag } from "components/utils/utils";
import EditorButton from "components/ui/EditorButton";
import TagLink from "components/ui/TagLink";
import { Breakpoint } from "antd/lib/_util/responsiveObserve";
import { useQuery } from "react-query3";

const tokensColumns: CustomColumns<AppToken>[] = [
  {
    title: "Created",
    dataIndex: "created",
    key: "created",
    editable: false,
    render: (text, record) => formatDate(text),
  },
  {
    title: "Expiration",
    dataIndex: "expiration",
    editable: false,
    key: "expiration",
    render: (text, record) => formatDate(text),
  },
  {
    title: "Revocation Date",
    dataIndex: "revocation Date",
    editable: false,
    key: "revocation",
    render: (text, record) => record?.revoked && formatDate(text),
  },
  {
    title: "Role",
    dataIndex: "role",
    editable: false,
    key: "role",
  },
  {
    title: "Revoked",
    dataIndex: "revoked",
    editable: false,
    key: "revoked",
    render: (text, record) => (record.revoked ? "Yes" : "No"),
  },
  {
    width: "fit-content",
    dataIndex: "actions",
    editable: false,
    render: (text, record) => {
      return (
        <Space>
          <CopyButton text={record.token} />
          <DeleteButton
            allowedWithOneWayGitSync={true}
            resource={{
              ...record,
              resourceInfo: {
                ...record?.resourceInfo,
                self: `/apptoken/${record?.id}`,
              },
            }}
            refetch={[`/identity/${record?.identity?.id}/apptoken`]} />
        </Space>
      );
    },
  },
];

const scriptsColumns: CustomColumns<Script>[] = [
  {
    key: "name",
    title: "Name",
    dataIndex: "name",
  },
  {
    key: "fullPath",
    title: "Path",
    dataIndex: "fullPath",
  },
  {
    key: "description",
    title: "Description",
    dataIndex: "description",
  },
  {
    key: "environment",
    title: "Environment",
    dataIndex: "environment",
    width: "min-content",
    align: "center",
  },
  {
    key: "tags",
    title: "Tags",
    dataIndex: "tags",
    editable: false,
    render: (value, record: Script) => {
      return record.tag?.length > 0
        ? record?.tag?.map((tag) => (
          <Tag key={tag?.name} color={tag?.color}>
            {tag?.name}
          </Tag>
        ))
        : null;
    },
  },
  {
    width: "fit-content",
    dataIndex: "actions",
    render: (text, record) => {
      return (
        <Space>
          <EditorButton to={`/admin/automation/scripts/${record?.fullPath}`} allowedWithOneWayGitSync={false} />
          <DeleteButton
            allowedWithOneWayGitSync={false}
            resource={{
              ...record,
              resourceInfo: {
                ...record?.resourceInfo,
                self: `${record?.resourceInfo?.parent}/${record?.id}`,
              },
            }} />
        </Space>
      );
    },
  },
];

const schedulesColumns: CustomColumns<Schedule>[] = [
  {
    title: "Script",
    key: "script",
    editable: false,
    render: (text, record) => (
      <TagLink
        to={`/admin/automation/scripts/${record?.script}`}
        text={record?.script}
      />
    ),
  },
  {
    title: "Description",
    dataIndex: "description",
    key: "description",
    editable: false,
  },
  {
    title: "Next Execution",
    key: "nextExecution",
    editable: false,
    render: (text, record) => formatDate(record.nextExecution),
  },
  {
    title: "Environment",
    key: "environment",
    dataIndex: "environment",
    editable: false,
    render: (text) => (text ? text : "Default"),
  },
  {
    key: "runAs",
    title: "Run As",
    editable: false,
    render: (text, record) => record?.credential || "Default",
  },
  {
    width: "fit-content",
    dataIndex: "actions",
    editable: false,
    render: (text, record) => {
      return (
        <Space>
          {/* <Modal>
            <EditModal record={record} />
          </Modal> */}
          <DeleteButton
            allowedWithOneWayGitSync={false}
            resource={{
              ...record,
              resourceInfo: {
                ...record?.resourceInfo,
                self: `/schedule/${record?.id}`,
              },
            }}
            refetch={[`/identity/${record?.identity?.id}/schedule`]} />
        </Space>
      );
    },
  },
];

const jobsColumns: CustomColumns<Job>[] = [
  {
    title: "Id",
    key: "id",
    dataIndex: "id",
    responsive: ["md" as Breakpoint],
    render: (text, record) => <TagLink text={text} to={`/admin/automation/jobs/${record.id}`} />,
  },
  {
    title: "Status",
    key: "status",
    render: (text: Date, record: Job) => setStatusTag(record?.status),
  },
  {
    title: "Created",
    dataIndex: "createdTime",
    key: "createdTime",
    render: (text: Date, record: Job) => formatDate(text),
  },
  {
    title: "Start Time",
    dataIndex: "startTime",
    key: "startTime",
    render: (text: Date, record: Job) => formatDate(text),
  },
  {
    title: "End Time",
    key: "endTime",
    dataIndex: "endTime",
    render: (text: Date, record: Job) => formatDate(text),
  },
];

export default function IdentityPage() {
  const { id } = useParams();

  const { data: identity } = useQuery<Identity>(`/identity/${id}`);
  const { data: apptokens } = useQuery<AppToken[]>(`/identity/${id}/apptoken`);
  const { data: scripts } = useQuery<Script[]>(`/script`);
  const { data: jobs } = useQuery<JobQuery>(`/identity/${id}/job`);
  const { data: schedules } = useQuery<Schedule[]>(`/identity/${id}/schedule`);

  let tableProps = {
    tokens: {
      title: "Tokens",
      data: apptokens,
      columns: tokensColumns,
    },
    scripts: {
      title: "Scripts",
      data: scripts,
      columns: scriptsColumns,
    },
    jobs: {
      title: "Jobs",
      data: jobs?.page?.reverse(),
      columns: jobsColumns,
    },
    schedules: {
      title: "Schedules",
      data: schedules,
      columns: schedulesColumns,
    },
  };

  return (
    <Page resource={identity}>
      <PageHeader>
        <ResourceInfo
          title={capitalize(identity?.name)}
          subTitle={`#${identity?.id}`}
        />
      </PageHeader>
      <PageContentBase>
        <Col span={24}>
          <Layout>
            <Layout.Content>
              <Card bordered={false}>
                <Tabs tabPosition="top" >
                  <Tabs.TabPane tab="Tokens" key="tokens">
                    <Table
                      pagination={{ defaultPageSize: 5 }}
                      {...tableProps.tokens}
                    />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Scripts" key="scripts">
                    <Table
                      pagination={{ defaultPageSize: 5 }}
                      {...tableProps.scripts}
                    />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Jobs" key="jobs">
                    <Table
                      pagination={{ defaultPageSize: 5 }}
                      {...tableProps.jobs}
                    />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Schedules" key="schedules">
                    <Table
                      pagination={{ defaultPageSize: 5 }}
                      {...tableProps.schedules}
                    />
                  </Tabs.TabPane>
                </Tabs>
              </Card>
            </Layout.Content>
          </Layout>
        </Col>
      </PageContentBase>
    </Page>
  );
}
